@use 'base/mixins' as m;
@use 'base/variables' as v;


.boletos {
  &__grid {
    @include m.contenedor-sm;
    @include m.grid(1, 5rem);
  }
}
.boleto {
  width: min(100% - 5rem, 65rem);
  margin: 0 auto;
  padding: 6rem;
  border-radius: 2rem;
  color: v.$blanco;
  position: relative;

  &::before,
  &::after {
    position: absolute;
    content: '';
    background-color: v.$blanco;
    width: 5rem;
    height: 5rem;
    top: calc(50% - 2.5rem);
  }

  &::before {
    clip-path: circle(50% at 25% 50%);
    left: 0;
  }
  &::after {
    clip-path: circle(50% at 75% 50%);
    right: 0;
  }

  &--presencial{
    background-color:hsla(178,100%,39%,1);
    background-image:
    radial-gradient(at 94% 70%, hsla(209,100%,47%,1) 0px, transparent 50%),
    radial-gradient(at 16% 90%, hsla(178,100%,39%,1) 0px, transparent 50%),
    radial-gradient(at 40% 20%, hsla(209,100%,47%,1) 0px, transparent 50%),
    radial-gradient(at 96% 90%, hsla(178,100%,39%,1) 0px, transparent 50%),
    radial-gradient(at 3% 6%, hsla(209,100%,47%,1) 0px, transparent 50%),
    radial-gradient(at 84% 23%, hsla(178,100%,39%,1) 0px, transparent 50%),
    radial-gradient(at 58% 90%, hsla(209,100%,47%,1) 0px, transparent 50%);
  }
  &--virtual{
    background-color: v.$secundario;
  }
  &--gratis{
    background-color: v.$azul;
  }

  &__logo {
    @include m.logo;
    @include m.texto-fluido(2rem, 4rem);
  }
  &__plan {
    font-size: 2rem;
    text-transform: uppercase;
    margin: 3rem 0 0 0;
  }
  &__precio {
    margin: 3rem 0 0 0;
    font-weight: v.$black;
    font-size: 4rem;
  }

  &__enlace-contenedor {
    margin-top: 5rem;
    display: flex;
    justify-content: center;
  }

  &__enlace {
    @include m.boton(v.$azul, v.$blanco);
    width: auto;
  }

  $modificador: &;

  &--acceso {
    width: 30rem;
    height: 50rem;
    padding: 10rem 3rem;
    @include m.tablet {
      width: 60rem;
      height: 30rem;
      padding: 6rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    #{$modificador}__logo {
      @include m.texto-fluido(2rem,3rem);
      margin: 0;
    }
    #{$modificador}__codigo {
      font-size: 4rem;
      font-weight: v.$bold;
      text-align: center;
      position: absolute;
      bottom: 7rem;
      margin: 0;
      left: 0;
      width: 100%;

      @include m.tablet {
        rotate: 90deg;
        transform-origin: 0 0;
        top: 0;
        left: calc(100% - 5rem);
        width: 30rem;
      }
    }
    #{$modificador}__nombre {
      font-size: 2.4rem;
      text-transform: uppercase;
      font-weight: v.$black;
    }
  }
  &::before {
    clip-path: circle(50% at 50% 25%);
    left: calc(50% - 2.5rem);
    top: 0;
    @include m.tablet {
      clip-path: circle(50% at 25% 50%);
      left: 0;
      top: calc(50% - 2.5rem);
    }
  }
  &::after {
    clip-path: circle(50% at 50% 75%);
    left: calc(50% - 2.5rem);
    top: calc(100% - 5rem);
    @include m.tablet {
      clip-path: circle(50% at 75% 50%);
      left: calc(100% - 5rem);
      top: calc(50% - 2.5rem);
    }
  }
}